@import url(https://fonts.googleapis.com/css?family=Skranji);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
html {
	margin: 0 auto;
}

body {
	font-size: 62.5%;
	background: rgb(255, 255, 255);
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.home {
	background-image: url(/ja/static/media/home-background.6e30c328.jpeg);
	background-repeat: no-repeat;
	background-size: 170%;
	background-position-x: 5%;
	background-position-y: 32%;
	height: 100vh;
	opacity: 0.8;
	position: relative;
	display: flex;
	margin-bottom: 150px;
}

.home .home-overlay {
	background-color: #383d44;
	height: 100vh;
	position: relative;
}

.home-container {
	height: 100%;
	width: 50%;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	padding-top: 30em;
	color: #fff;
	font-family: Sitara Arial, Helvetica, sans-serif;
	font-style: normal;
	font-weight: normal;
	text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.home-container h1 {
	margin: 0;
	font-size: 6.5rem;
	margin-right: 150px;
	font-weight: bold;
	line-height: 156px;
}

.home-container p {
	margin: 0;
	font-size: 3.6em;
	margin-right: 150px;
	margin-top: 0;
}

.home-container span {
	color: #f0e700;
}

.home-container button {
	background: #3d5579;
	border: none;
	box-shadow: 1px 1px rgba(0, 0, 0, 0.25);
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 2.4em;
	font-family: Skranji;
	width: 160px;
	height: 55px;
	margin: 30px 150px 10px 33%;
	text-shadow: 0px 100px 1000px rgba(0, 0, 0, 0);
}

.home-container button p {
	display: inline;
	font-size: 1em;
	text-shadow: 1px 1px #000;
	margin: 0;
}

.home-container button:hover {
	background: #263041;
}

.icons {
	width: 50%;
	display: flex;
	flex-direction: column;
	padding-top: 25em;
	color: #fff;
	font-family: Sitara Arial, Helvetica, sans-serif;
	font-style: normal;
	font-weight: normal;
	text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.icons a {
	width: 0;
	text-decoration: none;
	color: #fff;
}

.icons .github {
	cursor: pointer;
	margin: 20px 0;
	background: #000;
	border-radius: 30px;
}
.github:hover {
	opacity: 0.8;
}

.icons .linkedin {
	border: 1px solid #000;
	cursor: pointer;
	margin: 20px 0;
	padding: 5px 10px;
	background: #0077b5;
	border-radius: 10px;
}
.linkedin:hover {
	opacity: 0.8;
}

.icons .twitter {
	cursor: pointer;
	margin: 20px 0;
	color: #1ca1f2;
}
.twitter:hover {
	opacity: 0.8;
}

.about {
